@import url(https://fonts.googleapis.com/css2?family=Dela+Gothic+One&family=M+PLUS+1p:wght@500&display=swap);
body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.bg.yellow{background-color:#fff100}.bg.orrange{background-color:#f39800}.bg.blue{background-color:#37bef0}.bg.gray{background-color:#eaeaea}.t-normal{font-weight:normal}.sp_style{display:inline-block}.container{min-height:100vh}header{z-index:1;border-radius:2rem;border:3px solid #fff;position:relative}header ::before{content:"";width:calc(100% - 6px);height:calc(100% - 6px);border:3px solid #000;border-radius:2rem;position:absolute;top:3px;left:3px}header img.App-logo{display:inline-block;width:80%;max-width:384px;height:auto}header .inner-header{padding:1.5rem 0 .8rem 0;margin:3px;border-radius:1.7rem;text-align:center;position:relative}header .inner-header.yellow::before{content:"";width:calc(100% - 28px);height:calc(100% - 28px);border:3px solid #fff100;border-radius:1.7rem;position:absolute;top:14px;left:14px}header .inner-header.orrange::before{content:"";width:calc(100% - 28px);height:calc(100% - 28px);border:3px solid #f39800;border-radius:1.7rem;position:absolute;top:14px;left:14px}header .inner-header.blue::before{content:"";width:calc(100% - 28px);height:calc(100% - 28px);border:3px solid #37bef0;border-radius:1.7rem;position:absolute;top:14px;left:14px}header .inner-header.gray::before{content:"";width:calc(100% - 28px);height:calc(100% - 28px);border:3px solid #eaeaea;border-radius:1.7rem;position:absolute;top:14px;left:14px}header h2{font-family:"Dela Gothic One",cursive;font-size:2.4rem}header p.subTitle{font-family:"M PLUS 1p",sans-serif;font-size:1.2rem}header img.congrats-image{width:100%;height:auto;max-width:350px}main{border-radius:2rem;border:3px solid #eaeaea;padding:1rem}main .Bobo-icon{width:1.8rem;height:auto;position:relative;top:-1px}main .ttlNum{background-color:#e6faff;text-align:center;font-size:1.4rem;font-weight:bold}main p:not(.ttlNum){font-size:1rem}main .qimage{width:100%;height:auto}main .q-set{font-weight:bold}footer a,footer small{font-size:.8rem}.b-kaitou{width:100%;background:#ec6a38;border:1px solid #ec6a38;border-radius:2rem;font-size:1.26rem;font-family:"M PLUS 1p",sans-serif;text-align:center}@media(min-width: 576px){.container{max-width:100%;min-height:100vh}header h2{font-size:3rem}main .qimage{max-width:316px}.sp_style{display:none}.t-right{text-align:right}.b-kaitou{max-width:364px;margin:0 auto;display:inherit}}
